.hero-carousel-panel
{
/*	border: solid 0px rgba(255, 255, 255, 1);*/
	min-height:500px;
	height:500px;
	position:relative;
	overflow:hidden;
}

.hero-content-panel, hero-control-overlay-panel
{
	width:100%;
	height:100%;
	min-width:100%;
	min-height:100%;
}

.hero-content-panel
{
/*	background-color:red;*/
	z-index:0;
}
.hero-content-panel > div:nth-child(2)
{
/*	border:solid 1px red;*/
}
.hero-content-panel > div:nth-child(1), .hero-content-panel > div:nth-child(3)
{
	border:solid 1px green;
	display:none;
}

.hero-control-overlay-panel
{
	background-color:rgba(255,255,255,0);
	background-image:linear-gradient(180deg, 
	var(--mw-dark-purple-default) 0px, 
	rgba(0,0,0,0) 5vh,
	rgba(0,0,0,0) calc(100% - 5vh),
	var(--mw-dark-purple-default) calc(100% )
	);
	position:absolute;
	top:0px;
	left:0px;
	right:0px;
	bottom:0px;
	width:calc(100%);
	height:100%;
	min-width:100%;
	min-height:100%;
	display:flex;
	flex-direction:column;
	flex: 1 1 100%;
	z-index:1;
}


.slider-button-panel
{
/*	border: solid 1px white;*/
	background-color:rgba(0, 0, 0, 0);
	flex: 0 0 48px;
	display:flex;
	width:100%;
	cursor:pointer;
	text-align:right;
	justify-content:flex-end;
}
.slider-button
{
/*	border: solid 1px red;*/
	font-family: 'Gilroy-Bold';
	font-size:32px;
	flex: 0 0 48px;
	text-align:center;
	line-height:32px;
	padding-top:8px;
	border: solid 0px red;
	border-radius:20px;
	box-shadow: 0px 0px 20px 20px var(--mw-purple-01);
	background-color: var(--mw-purple-01);
	color: var(--mw-lite-purple-default);
}
.slider-button:hover
{
	transition: all ease-in-out 200ms;
	box-shadow: 0px 0px 20px 20px var(--mw-purple-04);
	background-color: var(--mw-purple-04);
	color: var(--default-white);
}


.slider-center-column
{
/*	border: solid 1px white;*/
	background-color:rgba(0, 0, 0, 0);
	flex: 1 1 auto;
	display:flex;
	flex-direction:column;
	text-align:center;
}




/* hero card panel */
.hero-card-panel
{
	border: solid 0px red;
	width:100%;
	text-align:center;
	clip: rect(0, 100%, 100%, 0);
}
.hero-content-image
{
	width:120%;	
}